@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;300;400;500;700;900&display=swap');

* {
    padding: 0;
    margin: 0;
    cursor: url('../resources/figmamouse16.svg'), auto;
    background-color: #2c2c2c;
}

iframe {
    width: 90vw;
    height: 90%;
    border: none;
}

.figma-wrapper {
    transform: translateY(calc((1.5rem + 20px)));
    display: flex;
    height: calc(100vh - 1.5rem - 20px);
    justify-content: center;
    align-items: center;
}

.App {
    font-family: 'Inter', sans-serif;
}

@media only screen and (max-width: 768px) {
    /* For mobile phones: */
    .home-main {
        margin: 0 10vw;
        width: 80;
    }
  }


  @media only screen and (min-width: 769px) {
    /* For tablet+: */
    .home-main {
        margin: 0 20vw;
        width: 60vw;
    }
  }

.home-main {
    height: 100vh;
    color: white;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-around;
}

.home-main-name {
    padding-bottom: 20px;
    width: inherit;
    color: white;
}

.home-main-name-subtext {
    font-size: 1.4rem;
    color: white;
}

.home-main-body {
    width: inherit;
}

.home-main-name h1 {
    font-weight: 700;
    font-size: 4rem;
}

.home-main-name h1 > span:hover {
    background-image: linear-gradient(to right, #0c8ce9 0%, #0c8ce9 100%);
    background-repeat: repeat-x; 
    background-position: 0 83%; 
    background-size: 100% 3px; 
}

.home-main-header {
    padding-bottom: 1rem;
}

.home-main-header p {
    font-weight: 300;
    font-size: 1.3rem;
    margin: 0 auto;
    line-height: 1.5em;
}

.home-main-header p > span:hover {
    background-image: linear-gradient(to right, #0c8ce9 0%, #0c8ce9 100%);
  background-repeat: repeat-x; 
  background-position: 0 95%; 
  background-size: 100% 3px; 
}

.home-main-body p {
    font-weight: 300;
}

.home-main-body p > span:hover {
    background-image: linear-gradient(to right, #0c8ce9 0%, #0c8ce9 100%);
  background-repeat: repeat-x; 
  background-position: 0 97%; 
  background-size: 100% 3px; 
}

.navitem {
    color: #E4E4E4;
    font-size: 22px;
    margin: 0 10px 0 10px;
}

.nav-text {
    color: #E4E4E4;
}

.navitem:active {
    font-weight: bold;
    color: #E4E4E4;
    font-size: 22px;
    margin: 0 10px 0 10px;
}

.active {
    font-weight: bold;
    color: #E4E4E4;
    font-size: 22px;
    margin: 0 10px 0 10px;
}

.home-col {
    padding: 25px 50px 100px 50px;
}



.home-footer {
    text-align: center;
    color: grey;
    font-size: 17px;
    padding-top: 100px;
}

.home-footer p > span:hover {
    background-image: linear-gradient(to right, #0c8ce9 0%, #0c8ce9 100%);
  background-repeat: repeat-x; 
  background-position: 0 97%; 
  background-size: 100% 3px; 
}

.coverpic {
    height: 200px;
}

.links {
    text-decoration: none;
}

#projects {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 50px;
    padding-bottom: 100px;
}

.aboutheader {
    text-align: center;
    margin-top: 100px;
    font-size: 42px;
    font-weight: bold;
}

.aboutpic {
    height: 500px;
}

.about-row {
    width: auto;
}

.about-col {
    padding: 50px 50px 50px 50px;
    text-align: center;
}

.about-p {
    font-size: 20px;
}

.contact-form {
    text-align: center;
    width: 50%;
    margin: 0 auto;
}


#home {
    color: #0D3B11;
    padding: 100px 25px 100px 25px;
}