@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;300;400;500;700;900&display=swap');

.about {
    background-color: #121212;
    font-family: 'Inter', sans-serif;
}

@media only screen and (max-width: 1028px) {
    /* For mobile phones: */
    .about-content {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .aboutpic img {
        width:auto;
        height:60vh;
    }
    
    .about-content-wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 10vh 5vw 0 5vw;
        gap: 2vw;
    }
    
    .about-text-wrapper {
        display: flex;
        flex-direction: column;
        gap: 20px;
        padding: 20px 0px;
    }
  }


  @media only screen and (min-width: 1029px) {
    /* For tablet+: */
    .about-content {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        min-height: calc(100vh - 60px);
    }

    .aboutpic img {
        width:auto;
        height:60vh;
    }
    
    .about-content-wrapper {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        padding: 5vh 10vw;
        gap: 2vw;
    }
    
    .about-text-wrapper {
        display: flex;
        flex-direction: column;
        gap: 20px;
        padding: 100px;
    }
    
  }

h1 {
    color: #f4f4f4;
}

p {
    color: #f4f4f4;
    font-weight: 300;
    line-height: 1.5;
}

a > p {
    cursor: pointer;
    text-decoration: underline;
    text-decoration-color: #f4f4f4;
}