@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;300;500;700;900&display=swap');

* {
    font-family: 'Inter', sans-serif;
}

@media only screen and (max-width: 768px) {
    /* For mobile phones: */

    .proj-img-wrapper img {
        max-width: 65vw;
    }

    .project-tile {
        flex-direction: column;
        width: 80vw;
        padding: 20px 0px 40px 0px;
        display: flex;
        align-items: center;
        border-radius: 24px;
        flex: none;
        order: 2;
        flex-grow: 0;
        background: #2c2c2c;
        cursor:pointer;
        border: solid 2px #444444;
        gap: 1rem;
    }

    .project-tile-content {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        padding: 0px;
        gap: 1.5rem;
        max-width: 70vw;
        flex: none;
        order: 1;
        flex-grow: 0;
        cursor:pointer;
        color: whitesmoke;
        background: inherit;
    }
    
    .proj-img-wrapper {
        display: flex;
        min-height: 250px;
        align-items: center;
        background: inherit;
        max-width: 70vw;
        
    }

    .proj-img-wrapper img {
        width: 100%;
        height: auto;
        cursor:pointer;
        background: inherit;
    }
    
  }


  @media only screen and (min-width: 769px) {
    /* For tablet+: */
    .project-tile {
        flex-direction: row;
        width: 60vw;
        padding: 40px 60px;
        gap: 80px;
        display: flex;
        flex-direction: row;
        align-items: center;
        padding: 40px 65px;
        gap: 80px;
        width: 60vw;
        border-radius: 24px;
        flex: none;
        order: 2;
        flex-grow: 0;
        background: #2c2c2c;
        cursor:pointer;
        border: solid 2px #444444;
    }

    .project-tile-content {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        padding: 0px;
        gap: 25px;
        max-width: 30vw;
        flex: none;
        order: 1;
        flex-grow: 0;
        cursor:pointer;
        color: whitesmoke;
        background: inherit;
    }
    
    .proj-img-wrapper {
        display: flex;
        min-height: 250px;
        align-items: center;
        background: inherit;
        max-width: 25vw;
        
    }

    .proj-img-wrapper img {
        width: 100%;
        height: auto;
        cursor:pointer;
        background: inherit;
    }

  }


.project-tile {
    transition: all 100ms cubic-bezier(0.52, 0.16, 0.36, 0.72);
}

.project-tile:hover {
    background-color: #383838;
}

