.text-header {
    background: inherit;
}

.text-header h4 {
    font-weight: 700;
    font-size: 1.5rem;
    margin-block-end: 0;
    margin-block-start: 0;
    cursor:pointer;
    background: inherit;
}

.project-text {
    background: inherit;
}

.project-text p {
    font-weight: 300;
    font-size: 1rem;
    cursor:pointer;
    background: inherit;
}